export default {
	data(){
		return{
			category:[]
		}
	},
	mounted(){
		//修改页面标题
		uni.setNavigationBarTitle({
			title:"分类"
		});
		this.getList();
	},
	methods:{
		getList(){
			this.request({
				url: 'http://157.122.54.189:9088/image/v1/vertical/category'
			})
			.then(result=>{
				console.log(result);
				this.category=result.res.category;
			})
		}
	}
}
